.menu-icon {
  color: whitesmoke;
  font-size: 30px;
  margin: 20px;
  z-index: 3;
  cursor: pointer;
}

.show-header {
  color: white;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  right: 0;
  background-color: #001220;
  opacity: 0.95;
  height: 65vh;
  width: 50%;
  border-bottom-left-radius: 50%;
  transition: all 0.5s;
  overflow: hidden;
  z-index: 2;
}

.hide-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  text-decoration: none;
  overflow: hidden;
  position: fixed;
  top: 0;
  right: 0;
  height: 0;
  width: 0;
  transition: all 0.5s;
}
ul li {
  color: white;
  list-style: none;
  margin-top: 25px;
}
ul a {
  color: white;
  text-decoration: none;
}

.active {
  border: 3px solid cyan;
  padding: 5px;
  border-radius: 10px;
}
.active a {
  font-weight: bold;
  color: cyan;
}

.contact {
  padding-top: 50px;
}
.contact img {
  height: 50vh;
  width: 100%;
}

input,
textarea {
  margin-top: 25px;
  border: none !important;
  background-color: #ebecf0 !important;
  background: #ebecf0;
  box-shadow: 5px 5px 0px #cacbce, -5px -5px 0px #ffffff !important;
  border-radius: 20px !important;
}
.form-control:focus {
  transform: scale(1.02);
}
.form-control {
  box-shadow: none;
}
.contact button {
  background-color: royalblue;
  color: white;
  font-weight: bold;
}

.footer-content {
  background-color: #001220;
  text-align: center;
  color: #fff;
}

.footer-icons svg {
  color: white;
}
.footer-icons:hover {
  color: cyan;
  transform: scale(1.5);
  cursor: pointer;
}

.animated2 {
  animation: project-animation 10s linear infinite;
}

@keyframes project-animation {
  0% {
    transform: translateX(-10%);
  }
  100% {
    transform: translateX(25%);
  }
}

@media (max-width: 750px) {
  .show-header {
    color: white;
    text-decoration: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    right: 0;
    background-color: #001220;
    opacity: 0.95;
    height: 100vh;
    width: 100%;
    transition: width 0.5s;
    overflow: hidden;
    border-bottom-left-radius: 50%;
    border-top-left-radius: 50%;
    border-left: 5px solid rgba(255, 255, 255, 0.445);
    z-index: 2;
  }
  .hide-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
    text-decoration: none;
    overflow: hidden;
    position: fixed;
    top: 0;
    right: 0;
    height: 0;
    width: 0;
    transition: all 0s;
  }
  .menu-icon {
    color: white;
    font-size: 45px;
  }
  .contact {
    margin-top: 0 !important;
    padding: 0 !important;
  }
  .contact-form {
    padding: 15px !important;
    margin: 15px;
  }
}
